.navbar {
  position: fixed;
  left: 0;
  right: 0;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 25px 40px;

  &__left {
    display: flex;
    align-items: center;
    flex: 1;
  }

  &__logo {
    margin-right: 30px;
    // cursor: pointer;
    text-decoration: none;
    width: 130px;
    height: auto;
  }

  &__title {
    margin: 20px;
    cursor: pointer;
    position: relative;
    text-transform: uppercase;
    font-size: $heading-4-font-size;
    font-weight: $heading-font-weight;
    letter-spacing: $spacing-extra-small;

    h1 {
      color: $tertiary-color;

      &:hover {
        color: $hover-text-color;
      }
    }


  }

  // cursor:  url('data:image/png;base64,'), auto;
}